.password-eye {
  position: absolute;
  top: 17px;
  right: 30px;
  font-size: 18px;
  color: var(--dark)
}

.cancel-btn {
  background-color: #E64C4C;
  box-shadow: 0px 10px 25px #E64C4C33 !important;
}

.no-data-avaible {
  opacity: 0.5;
  text-align: center;
}

.no-cod-span {
  margin-left: 30px;
  font-size: 14px;
  color: #969696 !important;
}
.available-offers img {
  margin-left: -5px;
}
.available-offers-modal {
  padding: 0 5px;
  max-height: 450px;
  overflow-y: scroll;

  .promo-code {
    background-color: var(--light-yellow);
    border-radius: 5px;
    border: 2px var(--yellow);
    border-style: dashed;
  }

  /* The radio-main */
  .radio-main {
    padding-left : 0;
  }


}

.available-offers-image{
  text-align: right;
  @media screen and (min-width: 320px) and (max-width: 767px) {
    text-align: left;
  }
}

.mobile-offer-image {
  img {
    width: 17px;
  }
  justify-content: flex-end !important;
}
.offer-details{
  color: #007AFF;
}

.mobile-edit-icon{
  font-size: 24px;
  cursor: pointer;
}

.cursor-pointer{
  cursor: pointer;
}

.fix-continue{
  width: 259px;
}

@media screen and (max-width: 991px) {
  .mobile-offer-image {
    font-size: 14px;
    margin-top: .5rem!important;
    justify-content: left  !important;
  }
}
.close-img{
  img{
    width: 25px !important;
  }
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    margin: 0; 
}
