.br-10 {
  border-radius: 10px !important;
}

.checkout-left {
  .card {
    border: none;
    box-shadow: 0px 50px 50px #00000008;
  }
  .payment-options-main {
    .card {
      box-shadow: none;
    }
  }

  .card-header {
    border-radius: 15px 15px 0 0;
    cursor: pointer;
    padding: .55rem 1rem;
    span {
      img {
        width: 18px;
        margin-top: -3px;
      }
    }
  }

  .login-number {
    position: relative;
    .country-code {
      position: absolute;
      left: 30px;
      border-right: 1px solid #E2E2E2;
    }
    .change-code {
      position: absolute;
      right: 30px;
    }
    .forgot-code {
      position: absolute;
      right: 15px;
    }
  }

  .check-mob-input {
    padding: 0 100px 0 60px !important;
  }

  .check-pass-input {
    padding: 0 100px 0 30px !important;
  }

  .common-input {
    height: 50px;
    padding: 0 15px 0 15px !important;
  }

  .cvv {
    height: 50px;
    text-align: center;

    &::placeholder {
      text-align: center;
    }
  }

  .otp-main {
    div {
      div:nth-child(odd) {
        width: 65px !important;
        height: 55px !important;;
        border: 1px solid #E2E2E2 !important;;
        border-radius: 20px !important;;
        padding: 15px !important;
        text-align: center !important;
        margin-right: 15px;
        @media only screen and (max-width: 991px) {
          width: 55px !important;
          height: 40px !important;;
        }
        input {
          width: 100% !important;
          border: none;
        }
      }

      div:nth-child(even) {
        width: 65px !important;
        height: 55px !important;;
        border: 1px solid #E2E2E2 !important;;
        border-radius: 20px !important;;
        padding: 15px !important;
        text-align: center !important;
        margin-right: 15px;
        @media only screen and (max-width: 991px) {
          width: 55px !important;
          height: 40px !important;;
        }
        input {
          width: 100% !important;
          border: none;
        }
      }
    }
  }

  .hide-cursor {
    input {
      color: transparent;
      text-shadow: 0 0 0 #000000;

      &:focus {
        outline: none;
      }
    }
  }

  .timer-box {
    border: 2px solid #5F78BE;
    border-radius: 5px;
    color: #5F78BE;
    padding: 5px 10px;
  }

  /* The radio-main */
  .radio-main {
    display: block;
    position: relative;
    padding-left: 35px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  /* Hide the browser's default radio button */
  .radio-main input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
  }

  /* Create a custom radio button */
  .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 20px;
    width: 20px;
    background-color: var(--white);
    border: 2px solid #73788B;
    border-radius: 50%;
  }

  /* On mouse-over, add a grey background color */
  .radio-main:hover input ~ .checkmark {
    background-color: var(--white);
  }

  /* When the radio button is checked, add a blue background */
  .radio-main input:checked ~ .checkmark {
    background-color: var(--white);
    border: 2px solid #0AAF51;
  }

  /* Create the indicator (the dot/circle - hidden when not checked) */
  .checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }

  /* Show the indicator (dot/circle) when checked */
  .radio-main input:checked ~ .checkmark:after {
    display: block;
  }

  /* Style the indicator (dot/circle) */
  .radio-main .checkmark:after {
    top: 3px;
    left: 3px;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #0AAF51;
  }
}

.qty-box {
  select {
    border-radius: 5px;
    border: 1px solid #73788B;
    font-family: 'Roboto-medium';
    font-size: 14px;
    width: 70px;
    height: 25px;
    padding: 0 10px 0 5px;
    -webkit-appearance: none;
    background-image: url("../../assets/imgs/select-drop.svg");
    background-repeat: no-repeat;
    background-position: 90%;

    &:focus {
      outline: none;
    }
  }
}

.saved-cards-main {
  button {
    border: 1px solid var(--yellow);
    height: 50px;
  }

  input {
    padding: 0 15px 0 15px !important;
    height: 50px;
  }

  .checkmark {
    top: 15px;
  }

  li {
    border-bottom: 1px solid var(--gray);
  }

  li:last-child {
    border-bottom: none;
  }
}

.upi-main {
  button {
    height: 50px;
  }

  input {
    padding: 0 15px 0 15px !important;
    height: 50px;
  }
}

.wallets-main {
  button {
    border: 1px solid var(--yellow);
    height: 50px;
  }

  .checkmark {
    top: 10px;
  }
}

.card-pay-main {
  button {
    height: 50px;
  }

  .checkmark {
    border: 2px solid #73788B;
    border-radius: 5px;
  }

  .radio-main input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }

  .radio-main input:checked ~ .checkmark {
    background-color: var(--yellow);
    border: 2px solid var(--yellow);
  }

  .radio-main .checkmark:after {
    left: 6px;
    top: 2px;
    width: 5px;
    height: 10px;
    border: solid var(--white);
    border-width: 0 2px 2px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    border-radius: 0;
    background: transparent;
  }

  .question {
    position: absolute;
    right: 12px;
    top: 12px;
  }
}

.netBank-main {
  select {
    padding: 0 15px;
    border-radius: 10px;
    height: 50px !important;
    border: 1px solid var(--gray);
    -webkit-appearance: none;
    background-image: url("./imgs/down-arrow.svg");
    background-repeat: no-repeat;
    background-position: 95%;
    font-family: 'Roboto-regular';
    font-size: 18px;
    color: var(--light-gray);

    &:focus {
      outline: none;
    }
  }
}

